<template>
  <section class="grid-table">
    <Grid :data="data" :columns="columns">
      <template #column="{ column }">
        <span class="grid-table__column">{{ column.title }}</span>
      </template>
      <template #cell="{ item, column }">
        <component
          :is="column.component || 'span'"
          :type="'success'"
          class="grid-table__cell"
        >
          {{ item }}
        </component>
      </template>
    </Grid>
    <Pagination v-model="page" />
  </section>
</template>

<script>
import Grid from '@/components/Grid/Grid'
import { computed, defineAsyncComponent } from 'vue'
import { useStore } from 'vuex'
import Pagination from '@/components/UIKit/Pagination'

export default {
  name: 'GridTable',
  components: {
    Pagination,
    Grid,
    Badge: defineAsyncComponent(() => import('@/components/UIKit/Badge'))
  },
  setup () {
    const store = useStore()
    const data = computed(() => store.getters['transactions/getTransactions'])
    const columns = computed(() => store.getters['transactions/getActiveColumns'])
    const perPage = computed(() => store.getters['transactions/getPerPage'])
    const totalCount = computed(() => store.getters['transactions/getTotalCount'])
    const page = computed({
      get: () => store.getters['transactions/getPage'],
      set: (val) => store.commit('transactions/SET_PAGE', val)
    })

    return {
      data,
      columns,
      page,
      perPage,
      totalCount
    }
  }
}
</script>

<style lang="scss">
.grid-table {
  &__column {
    font-size: 14px;
    line-height: 16px;

    color: #000000;
    opacity: 0.5;
  }
  &__cell {
    font-size: 14px;
    line-height: 16px;
  }
}
</style>
