<template>
  <section class="transactions">
    <header class="transactions__header">
      <h1 class="transactions__header__title">Transactions</h1>
    </header>
    <main class="transactions__container">
      <TransactionsGrid />
    </main>
  </section>
</template>

<script>
import TransactionsGrid from '@/views/admin/Transactions/TransactionsGrid'
import { useStore } from 'vuex'
import { onBeforeUnmount } from 'vue'
import transactionModule from '@/store/modules/transaction'

export default {
  name: 'Transactions',
  components: {
    TransactionsGrid
  },
  setup () {
    const store = useStore()
    if (!store.state.transactions) {
      store.registerModule('transactions', transactionModule)
    }
    onBeforeUnmount(() => {
      if (store.state.transactions) {
        store.unregisterModule('transactions')
      }
    })
  }
}
</script>

